.app-wrapper {
    display: grid;

    margin: 0 200px;

    grid-template-areas: "h h" "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 1fr 10fr;
    grid-gap: 10px;

    height: 100vh;
}

.app-wrapper-content {
    grid-area: c;
    background-color: #edeef0;
}